@import "color.scss";


.home{
    width: 100%;
    
    height: 100vh;
    background-color: $second;
    border-radius: 0 0 200px 200px;//T,R,B,L
        //this is giving border RADIUS to background color
        
    >main{
        position: absolute;
        text-transform: uppercase;
        display: flex;
        flex-direction: column;
        align-items: flex-end;
      
        width: 100%;
        height: 100%;
        padding: 2rem;
        justify-content: center;
        >h1{
            font:400 6rem $text1;
            color: $first;
        }
        >p{
            font:900 1rem $text1;//font:weight size style
            color: $first;
        }
    }
    &::before{//apply this in main tag
        content: "";
        width: 100%;
        height: 100%;
        position: absolute;
        background-image: url(../assets/technical2.jpeg);
        background-position: center/auto;
        background-repeat: no-repeat;
        background-size: cover;
        filter: blur(1px);//giving blur
        border-radius: 0 0 200px 200px;//T,R,B,L
        //this is giving border RADIUS to background img
    }
    
}   
.home2{
    height: 100vh;
    width: 100%;
    background-color: $second;
    padding: 500px 0;
    display: flex;
    align-items: center;
    justify-content: center;
    
    margin-top:-200px ;//it just cover the radius of upper surface
    >img{
        width: 50%;
        animation: imgUP 0.7s linear  infinite  alternate;
        
    }
    >div{
        width: 40%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        padding: 5rem;
        height: 100vh;
        >p{
            color: $first;
            word-spacing: 5px;
            font: 100 1.2rem $text2;
            text-transform: capitalize;
        }
    }
    
}

@keyframes imgUP{
    to{
        transform: translateY(-10px);//giving animaition in y direction
    }
}

.home3{
    width:100%;
    height: 120vh;
    background-color: $second;
    margin-top: -200px;
    display: flex;
    >div{
        width: 70%;
        height: 100vh;
        background-color: $first;
        border-radius: 0 200px 200px 0;//top-left top-right bottom-right bottom-left
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        padding: 2rem;
        >h1{
            text-transform: uppercase;
            border-bottom: 2px solid $dark;
            padding: 0.5rem;
            width: 30rem;
            color: $dark;
        }
        >p{
            padding: 5rem;
            letter-spacing: 1px;
            word-spacing: 7px;
            line-height: 170%;
            font: italic 100 1.2rem $text2;
        }
    }    
   
}
.home4{
    width:100%;
    height: 100vh;
    background-color: $second;
    // margin-top: -200px;
    display: flex;
    justify-content: flex-end;
    
    >div{
        // animation: imgUP 0.7s linear infinite alternate;
        background-color: $third;
        border-radius: 200px 0 0 200px;
        width: 70%;
        height: 70vh;
        padding: 2rem;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        >h1{
            color: $first;
            text-transform: uppercase;
            width: 140px;
            border-bottom: 2px solid $first;
            padding: 0.5rem;
        }
        >article{
            display: flex;
            margin: 2rem 0;
            flex-wrap: wrap;//how screen we minimmize it automatically wrapp
            justify-content: center;
            >div{
                animation: imgUP 0.7s linear infinite alternate;
                margin: 1rem;
                height: 10rem;
                width: 10rem;
                background-color: $first;
                border-radius: 50%;
                display: flex;
                flex-direction: column;
                align-items: center;
                justify-content: center;
                cursor: pointer;
                color: $dark;
                transition: all 0.5s;
                &:hover{
                    filter:invert(1);
                }
                svg{
                    font-size: 4rem;

                }
                p{
                    font: 900 1.2rem $text2;
                }
            }
        }
    }


}