@import "./color.scss";

footer{
    background-color: $first;
    height: 50vh;
   width: 100%;
   color: $dark;
   display: grid;
   grid-template-columns: 4fr 1fr;
   padding: 2rem;
   >div{
    display: flex;
    flex-direction: column;
    justify-content: center;
   >h1{
    font: 700 2rem $text1;//weight size style
    
    text-transform: uppercase;
    color: $third;
   }
   >p{
    font: 500 1.2rem $text1;
   }
   >h5{
    font: 300 1.6rem $text1;
    margin: 1rem 0;
    text-align: center;
   }
   >div{
    display: flex;
    flex-direction: column;
    align-items: center;
    a{
        color: $dark;
        font: 900 1rem $text2;
        &:hover{
            color: $third;
        } 
    }
   }
    }

}